  },
  methods: {
    setTheme(color) {
      if (color === "red") {
        document.documentElement.style.setProperty("--main-hue", 200);
        document.documentElement.style.setProperty("--main-filter", "100%");
      } else {
        document.documentElement.style.setProperty("--main-hue", 300);
        document.documentElement.style.setProperty("--main-filter", "50%");
      }
    },
    // jsonData: 
    // id: id
    // pid: 
    /*
      [
        {
          name: '',
          id: '1001',
          pid: '-1',
        }
      ] 
    */
    jsonToTree(jsonData, id, pid) {
      let result = [],
        temp = {};
      for (let i = 0; i < jsonData.length; i++) {
        temp[jsonData[i][id]] = jsonData[i]; // id
      }
      for (let j = 0; j < jsonData.length; j++) {
        let currentElement = jsonData[j];
        let tempCurrentElementParent = temp[currentElement[pid]]; // 
        if (tempCurrentElementParent) {
          // 
          if (!tempCurrentElementParent["children"]) {
            // chindren
            tempCurrentElementParent["children"] = []; // children
          }
          tempCurrentElementParent["children"].push(currentElement); // 
        } else {
          // 
          result.push(currentElement);
        }
      }
      return result;
    },
    resetScroll() {
      this.$refs.scrollContent.scrollTop = 0;
    }
  }
};
