import Moveable from "moveable";

export default class Move {
  constructor(dom, options) {
    this.options = {
      className: "draggable", // class
      target: null, // moveable,
      origin: false, // moveable,default: true
      edge: false, // resize,scale
      zoom: 1, // moeable,default: 1,
      draggable: true, // 
      edgeDraggable: false, // , default: false
      resizable: true, // 
      keepRatio: true,
      // renderDirections: ["n", "nw", "ne", "s", "se", "sw", "e", "w"]
      renderDirections: ["nw", "ne", "se", "sw"],
      snappable: true,
      elementSnapDirections: true,
    };
    Object.assign(this.options, options);
    this.moveable = new Moveable(
      dom, // moveable
      {
        ...this.options,
      }
    );
    this.moveable.on(
      "drag",
      ({
        target, // DOM moveabletarget
        beforeTranslate, // [left, top] 
      }) => {
        target.style.transform = `translate(${beforeTranslate[0]}px, ${beforeTranslate[1]}px)`;
      }
    );
    this.moveable.on(
      "resize",
      ({
        target, // DOM moveabletarget
        width, // number (target)css
        height, // number (target)css
        drag, // ,onDrag
      }) => {
        target.style.width = `${width}px`;
        target.style.height = `${height}px`;
        target.style.transform = `translate(${drag.beforeTranslate[0]}px, ${drag.beforeTranslate[1]}px)`;
      }
    );
  }
  getMoveable() {
    return this.moveable;
  }
  destroy() {
    this.moveable.destroy();
  }
}
