class EventBus {
    constructor() {
      this._events = [];//
    }
  
    /**
     * 
     * @param event
     * @param fn
     */
    on(event, fn) {
      if (Array.isArray(event)) {
        for (let i = 0, l = event.length; i < l; i++) {
          this.on(event[i], fn)
        }
      } else {
        // push, push
        (this._events[event] || (this._events[event] = [])).push(fn)
      }
    }
  
    /**
     * 
     * @param event
     * @param fn
     */
    once(event, fn) {
      let _self=this;
      function handler() {
        _self.off(event, handler);
        fn.apply(null,arguments);//emiton
      }
  
      handler.fn = fn;//off
      this.on(event, handler);
    }
  
    /**
     * 
     * @param event
     * @param fn
     */
    off(event, fn) {
      //
      if (!arguments.length) {
        this._events = [];
      }
      //
      if (Array.isArray(event)) {
        for (let i = 0, l = event.length; i < l; i++) {
          this.off(event[i], fn)
        }
      }
      const cbs = this._events[event];
      if (!cbs) {
        return;
      }
      //
      if (arguments.length == 1) {
        this._events[event] = null
      }
      let cb, i = cbs.length
      while (i--) {
        cb = cbs[i]
        if (cb === fn || cb.fn === fn) { //cb.fn===fnonce
          cbs.splice(i, 1)
          break
        }
      }
    }
  
    /**
     * 
     * @param event
     */
    emit(event) {
      //oncethis._eventsfn, 
      let cbs = [...this._events[event]];
      if (cbs) {
        for (let i = 0, l = cbs.length; i < l; i++) {
          try {
            cbs[i].apply(null,[...arguments].slice(1))
          } catch (e) {
            new Error(e, `event handler for "${event}"`)
          }
        }
      }
    }
  }
const Bus = new EventBus()
if (!Bus) {
  Bus = new EventBus()
}
export default Bus