<template>
  <div class="home">
    <svg class="icon" aria-hidden="true">
      <use xlink:href="#icon-kongxian"></use>
    </svg>
    <Button @click="btnClick">js</Button>
    <Button @click="serverClick">node.js_http</Button>
    <Button @click="expressClickGET">node.js_express_GET</Button>
    <Button @click="expressClickPOST">node.js_express_POST</Button>
    <div class="iptsDiv">
      <input type="text" class="iptText" />
      <input type="text" class="iptText1" />
      <input type="text" class="iptText" />
    </div>
    <div
      ref="box"
      class="box"
      style="width:500px;height:100px; border:1px solid black;overflow-x: hidden;overflow-y: auto;margin:20px;padding: 3px;"
    >
      <div v-for="item in items" :key="item.name">
        {{ item.time }}
      </div>
    </div>
    <Button
      @click="add"
      style="display:block;width:100px;height:20px;margin:20px;"
      ></Button
    >
    <div>
      <treeSearch></treeSearch>
    </div>
    <div :class="$style['test-css']">
      <div>
        <h3>Stylus</h3>
        <Stylus></Stylus>
      </div>
      <div>
        <h3>Less</h3>
        <TestLess></TestLess>
      </div>
    </div>
    <Button @click="addConfirm">confirm</Button>
    <BaseCropper :class="$style['cropper']"/>
    <TheTree />
  </div>
</template>

<script>
import homeJS from "../assets/js/home";
import treeSearch from "@/views/treeSearch";
import Stylus from '@/components/Stylus';
import TestLess from '@/components/TestLess';
import "../assets/css/iconfont/iconfont";
import BaseCropper from '@/components/BaseCropper.vue'
import TheTree from '@/views/ant-design/TheTree.vue'
import EventBus from '@/common/eventBus.js'

export default {
  name: "home",
  components: {
    treeSearch,
    Stylus,
    TestLess,
    BaseCropper,
    TheTree
  },
  data() {
    return {
      items: []
    };
  },
  methods: {
    btnClick() {
      EventBus.emit('home-change', 'Homeemit')
      homeJS.btnClick();
    },
    serverClick() {
      let params = {
        name: "admin",
        password: "123456"
      };
      this.$axios.get("/node", { params: params }).then(res => {
        console.log(res);
      });
    },
    expressClickGET() {
      let params = {
        name: "admin",
        password: "123456"
      };
      this.$axios.get("/Express/login/Express", { params: params }).then(res => {
        console.log(res);
      });
    },
    expressClickPOST() {
      let params = {
        name: "admin",
        password: "123456"
      };
      this.$axios.post("/Express/login", { params: params }).then(res => {
        console.log(res);
      });
    },
    add() {
      var div = this.$refs.box,
        time = new Date().getTime(),
        item = { time: time };

      this.items.push(item);
      div.scrollTop = div.scrollHeight;
    },
    addConfirm() {
      this.$confirm({
        title: '',
        content: 'xxx?'
      })
    }
  },
  mounted() {
    //FormData
    var formdata = new FormData();
    //append()keynamelaoliu
    formdata.append("name", "laoliu");
    console.log(formdata.getAll("name"));
    //append()keynamelaoliu2
    formdata.append("name", "laoliu2");
    //getkeyname
    console.log(formdata.get("name")); //laoliu
    //getAllkeyname
    console.log(formdata.getAll("name")); //["laoliu", "laoliu2"]

    //keynamelaoli
    formdata.set("name", "laoli");
    //getkeyname
    console.log(formdata.get("name")); //laoli
    //getAllkeyname
    console.log(formdata.getAll("name")); //["laoli"]

    console.log(sessionStorage.getItem('aaa'), new Date());
  }
};
</script>
